<template>
<div class="register">
  <div >
    <van-nav-bar
      title=""
      left-arrow
      @click-left="onClickLeft"/>  
  </div>

      <!--  -->
  <van-form>

    <!--  -->
    <van-cell-group>
     <van-field placeholder="" 
        type='number'
        maxlength="11"
        @blur="checkPhone"
        v-model="phone"
        left-icon="friends-o"
        :error='iserror'
        :error-message='iserrorMsg'

      />

    </van-cell-group>
  
     <!--  -->
    <!-- <van-field
        maxlength="6"
        placeholder=""
        left-icon="comment-o" 
        v-model="value1"
        >
        <template #button>
          <van-button size="small" type="warning" ></van-button>
          <van-button v-if="cutDownTime" size="small" type="primary">{{cutDownTime}}s</van-button>
        </template>
    </van-field> -->

        <!--  -->
    <van-field  
     
      placeholder="(812)" 
      left-icon="label-o"
      maxlength="12"
      minlength="1"
      
      v-model="nickname"
      />
        <!--  -->
    <van-field  
      type="password" 
      placeholder="(6-16)" 
      maxlength="16"
      minlength="6"
      left-icon="goods-collect-o"
      @blur="checkPassword"
      v-model="password"
    />
        <!--  -->
    <van-field  
      type="password" 
      v-model="conpassword"
      placeholder="" 
      maxlength="16"
      minlength="6"
      left-icon="goods-collect-o" 
      @blur="checkConpassword"
    />
        
        
        <!--  -->
    <van-field name="radio" label="">
      <template #input>
        <van-radio-group v-model="radio" direction="horizontal" >
          <van-radio name="1" class="a1"></van-radio>
          <van-radio name="0" class="a2"></van-radio>
        </van-radio-group>
      </template>
    </van-field>
  </van-form>


       <!--  -->
   <van-button round block type="info" native-type="submit" class="btn" @click="handle">
      
   </van-button>
      <!--  -->
   <div id="xieyi">
    <input type="checkbox" class="input" @click="xy" :checked="d"> &nbsp; <span class="xy"> <router-link to="/Agreement"></router-link></span> 
  </div>
</div>
</template>

<script>
    export default {
        data(){
          return{
            //
            iserror:false,
            iserrorMsg:'',
            text:"",
            radio: '1',
            phone:'',
            value1:'',
            // 
            nickname:"",
            //
            password:'',
            conpassword:"",
            d:false
       
          }
        },
        methods: {
          //

          //
            xy(){
              if(this.true){
                this.d=false;

              }else{
                this.d=true;
                // return true;
              }
              
            },

          //
            handle(){
              //let radio=this.radio;
            //   console.log(radio);
            //   if( this.checkPhone()  && this.checkPassword() ){
            //      this.$router.push("/")
            //   }
              if(this.d){
                if(this.nickname==""){
                  this.nickname = this.phone;
                }  
                this.axios.post('/user/register',"&phone="+this.phone +'&password=' + this.password+"&nickname="+this.nickname ).then( res => {
                // this.phone = res.data.result;
                // console.log(res.data.userInfo);
                  if (res.data.code == 1) {
                    this.$router.push("/login");
                  } else{
                    this.$toast.fail('');
                  }
                });

            } 
          }, 

          //
          checkConpassword() {
            //
            let password = this.password;
            let conpassword = this.conpassword;
            if (password == conpassword) {
              return true;
            } else {
              this.$toast({
                message: "",
                position: "middle",
                duration: 4000,
              });
              return false;
            }
          },

          //
          checkPassword() {
            let password = this.password;
            let passwordReg = /^[0-9A-Za-z\.\-_]{6,16}$/;
            if (passwordReg.test(password)) {
              this.perror = false;
              return true;
            } else {
              this.$toast({
                message: "",
                position: "middle",
                duration: 5000,
              });
              return false;

            }
          },
        
          // 
          checkPhone() {
            let phone = this.phone;
            let phoneReg =/^[1][3-9]\d{9}$/g; 
            
            if (phoneReg.test(phone)) {
              // 
              this.iserror = false;
              this.iserrorMsg='';
              return true;
            } else {
              //
              this.iserror = true;
              this.iserrorMsg='';
              return false;
            }
          },

          onClickLeft() {
            this.$router.push('/login')
          },

        },
        
    }
    
</script>

<style>

    /*  */
    .register #xieyi{
    margin-top: 20px;
    margin-left: 20px;
    color:#A7A7A7;
    }

    .register .xy a{
      color:  #F3AC3F;
    }

    /*  */
    .register #button{
        border-radius: 15px;
      }
      .register .btn{
        border-radius: 5px;
        width: 90%;
        background-color: #5ABCC8;
        margin: 0 auto;
        border: 0;
      }
      /*  */
        .register .van-radio__icon--checked .van-icon{
        background-color: #F3AC3F;
        border-color: #F3AC3F;
      }
</style>
 