<template>
  <div class="login">
   <van-image :src="require('../assets/logo.png')"  class="qc"/>



    <!--  -->
    <div>
        <van-field
         type='number'
          placeholder=""
          left-icon="friends-o"
          class="d1"
          maxlength='11'
          @blur="checkPhone"
          v-model="phone"
         

        />
        <van-field 
          left-icon="goods-collect-o" 
          placeholder="" 
          maxlength='16'
          minlength='6'
          class="d2"
          type="password" 
          @blur="checkPassword"
          v-model="password"
          >
          <!--  -->
          <template #button>
            <router-link to="/Update" >
              <van-button size="small" type="primary" id="btn1">
                 
              </van-button>
             </router-link>
          </template>
        </van-field>
    </div>
    <div class="regist">
      <router-link to="/Register" ></router-link>
    </div>

    <!--  -->
    <van-button round block type="info" native-type="submit" class="btn" @click="handle">
      
    </van-button>


    <!--  -->
    <!-- <div class="shejiao">
      <router-link to="" class="qq">
        <van-image :src="require('../assets/img/qq.png')" width="25" /> QQ
      </router-link>
      <router-link to="" class="wx">
        <van-image :src="require('../assets/img/wx.png')" width="25" />
      </router-link>
    </div> -->
  </div>
</template>

<script>
  export default {
    data(){
      return{
        //
        iserror:false,
        iserrorMsg:'',
        text:"",
        radio: '1',
        phone:'',
        value1:'',
      //
        password:'',
    
      }
    },
    methods: {
      //
      handle(){
        
         if(this.checkPhone() && this.checkPassword() ){
          //  console.log(this.phone,this.password);
          //  this.$router.push("/")
        //
        this.axios.post('/user/login',"phone="+this.phone +'&password=' + this.password).then( res => {
                  // this.phone = res.data.result;
                  // console.log(res.data.userInfo);
                  // 1
                if(res.data.code == 1){
                  // console.log(res.data.code)
                  // console.log(res.data.results)
                  this.$store.commit("login_mutations",res.data.results);
                 this.$router.push("/")
                } else {
                  // 
                   this.$toast.fail('');
                }
         });





        }
      },
      
      //
        checkPassword() {
            let password = this.password;
            let passwordReg = /^[0-9A-Za-z\.\-_]{6,16}$/;
            if (passwordReg.test(password)) {
              return true;
            } else {
              this.$toast({
                message: "",
                position: "middle",
                duration: 5000,
              });
              return false;

            }
          },

      //
      checkPhone() {
        let phone = this.phone;
        let phoneReg = /^1[3-9]\d{9}$/;
        if (phoneReg.test(phone)) {
          // 
        
          return true;
        } else {
          //
          this.$toast({
                message: "",
                position: "middle",
                duration: 5000,
              });
          return false;
        }
      },
      onClickLeft() {
        this.$router("/");
      },
      onClickLeft() {
        this.$rooter.push("/register");
      },
    }
  };
</script>

<!---->
<style>
 .login .van-icon-friends-o::before {
   color: #fff;
   font-size: 24px;
 }
 .login .van-icon-goods-collect-o::before {
   color: #fff;
   font-size: 24px;
   margin-top: 4px;
}
.login {
  background: url(../assets/img/dl.png) no-repeat 100%;
  background-size: 120%;
  height: 100%;
}
.login .qc{
  width: 100%;
}
.login .qc img{
  width: 60%;
  margin: 20% auto;
}
/*  */
.login .d1,.login .d2{
  background: transparent;
}
.login .van-cell::after{
  border: 0;
}
.login .d1,.login .d2{
    border-bottom: 1px solid #fff;
    padding: 10px 0;
    width: 85%;
    margin: 0 auto;
}
.login input{
  color: #fff;
}
.login input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
  color: #fff;
  font-size: 14px;
}
/*  */
.login #btn1 {
  background-color: #ccc;
  margin: 0 auto;
  border-radius: 20px;
  border: 0;
  width: 80px;
  /* background: transparent; */
}
/*  */
.login .regist a{
  color: #fff;
  font-size: 14px;
  font-weight: lighter;
  margin: 5% 7.5% 0;
  float: right;
}
.login .regist{
  height: 5%;
  clear: both;
}
/*  */
.login .btn {
  background: transparent;
  border: 2px solid #fff;
  width: 85%;
  margin: 5% auto 0;
}
/* QQ */
.login .shejiao {
  justify-content: space-between;
  display: flex;
  position: absolute;
  width: 100%;
  bottom: 20px;
}
.login .qq,
.login .wx {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #fff;
}
.login .qq {
  margin-left: 7.5%;
}
.login .wx {
  margin-right: 7.5%;
}

</style>