<template>
  <div class="home">
    <!--  -->
    <van-nav-bar title="" left-arrow fixed :border="show_border" :style="{backgroundColor:backgroundColor}">
      <template #left>
        <van-icon name="user-circle-o" :color="nva_icon_color" size="24" @click="myInfo" /> 
      </template>
      <template #right>
        <router-link to="/keepdiary">
          <van-icon name="edit" :color="nva_icon_color" size="24" />
        </router-link>
      </template>
    </van-nav-bar>
    <!--  -->

    <!--  -->
    <div class="top_bg">
      <h1></h1>
      <span></span>
    </div>
    <!--  -->

    <!--  -->
    <van-tabs v-model="active" swipeable lazy-render class="tabbar" sticky offset-top="46" animated>
      <van-tab :title="item.classify" v-for="(item,index) of classify" :key="index" :name="index+1" :offset-top="36">

        <!--  -->
        <lazy-component>
          <!--  -->
          <div v-for="(diary,index) of diarys" :key="index" >
            <router-link :to="`/mylv/${diary.jid}`" >
              <div class="articleItem">
                <!--  -->
                <div class="articleItem-header">
                  <van-image :round="avatar" width="4rem" :src="'http://127.0.0.1:8888/avatar/'+ diary.avatar" fit="cover" class="articleImg"/>
                  <div class="articleMsg">
                    <div>
                      <span>{{diary.nickname}}</span>
                      <!-- <h3 class="articleItem-time">{{this.moment.unix(diary.log_time).format('YMMDD')}}</h3> -->
                      <h3 class="articleItem-time">{{diary.log_time/1000 | datefmt('YYYY-MM-DD')}}</h3>
                    </div>
                    <h1>{{diary.journal_city}}</h1>
                  </div>
                </div>
                <!--  -->
                
                <!--  -->
                <div class="articleTitle">{{diary.journal_title}}</div>
                <div class="articleDes">{{diary.content}}</div>
                <!--  -->

                <!--  -->
                <van-swipe-cell class="journalImg" right-width="630" :disabled="true" stop-propagation >
                                                                                <!--  -->
                  <div v-for="(pic,pindex) of diary.pics" :key="pindex" @click.prevent="preview(diary.pics,pindex)" v-if="pindex < 3" >
                    <van-image
                      width="7.7rem"
                      height="7.7rem"
                      fit="cover"
                      lazy-load
                      :src="`http://127.0.0.1:8888/journal-pic/${pic.picture_pic}`"
                    >
                      <template v-slot:loading>
                        <van-loading type="spinner" size="20" />
                      </template>
                    </van-image>
                  </div>
                </van-swipe-cell>
                <!--  -->

                <!--  -->
                <div class="journalInfor">
                  <span>{{diary.browse || 0}}</span>
                  <span>{{diary.msg_number || 0}}</span>
                </div>
                <!--  -->
              </div>
            </router-link>
          <!--  -->
          </div>
          <!--  -->
          <van-divider dashed
            :style="{ color: '#ccc', borderColor: '#ccc', padding: '0 16px' }"
          >
            
          </van-divider>
          <!--  -->
        </lazy-component>

      </van-tab>
    </van-tabs>
    <!--  -->
  </div>
</template>

<style>
  .home{
    background: #eee;
  }
  .home .van-nav-bar__title{
    color: #fff;
  }
  .home .top_bg{
    height: 320px;
    background: url(../assets/img/home_top.jpg) no-repeat center;
    background-size: 100%;
  }
  .home .top_bg h1,.top_bg span{
    color: #fff;
    font-size: 2rem;
    margin-left: 0.5rem;
  }
  .home .top_bg span{
    width: 320px;
    font-size: 1.2rem;
    font-weight: lighter;
    display: block;
  }
  .home .top_bg::before{
    display: table;
    content: "";
  }
  .home .top_bg h1{
    margin-top: 210px;
  }
  .home .top_bg span{
    margin-top: 10px;
    line-height: 1.5rem;
  }


  .home .tabbar{
    margin: 10px 0 0;
  }

  .home .tabbar .van-tabs__line{
    background-color: #5ABCC8;
  }
  .home .tabbar .van-tab--active{
    color: #5ABCC8;
  }


  /*  */
  .home .articleItem{
    background: #fff;
    margin: 10px 0;
  }
  .home .articleItem-header{
    font-weight: 500;
    font-size: 16px;
    color: #1a1a1a;
    line-height: 16px;
    display: flex;
    flex-wrap: nowrap;
  }
  .home .articleImg{
    padding: 15px;
  }
  .home .articleImg img{
    border: 1px solid #5ABCC8;
  }
  .home .articleMsg{
    width: 100%;
    margin-top: 15px;
  }
  .home .articleMsg span{
    color: #5ABCC8;
  }
  .home .articleMsg div{
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
  }

  .home .articleItem-header h1{
    margin-top: 18px;
    color: #3a3a3a;
  }
  .home .articleItem-time{
    align-self: center;
    width: 100px;
    color: #ccc;
    font-weight: 300;
    font-size: 14px;
    text-align: right;
    padding-right: 15px;
  }
  .home .journalImg{
    margin: 15px auto 16px; 
  }
  .home .journalImg>div>div+div{
    margin-left: 3px;
  }
  .home .journalImg>div{
    display: flex;
    flex-wrap: nowrap;
    justify-content: start;
    box-sizing: border-box;
  }
  .home .articleDes{
    /* height: 65px; */
    font-size: 15px;
    overflow: hidden;
    font-weight: 400;
    text-overflow: ellipsis;
    line-height: 21px;
    letter-spacing: normal;
    color: #444;
    padding: 0 15px;
    text-indent:2em;

    display:-webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient:vertical;
  }
  .home .articleTitle{
    font-size: 18px;
    overflow: hidden;
    font-weight: bold;
    color: #444;
    padding: 0 15px;
    margin-bottom: 10px;
    display:-webkit-box;
    -webkit-line-clamp:1;
    -webkit-box-orient:vertical;
  }
  .home .journalInfor{
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-end;
    color: #ccc;
    font-size: 14px;
    padding-bottom: 15px;
  }
  .home .journalInfor>span{
    margin-right: 15px;
  }
</style>

<script>
import { ImagePreview } from "vant"; // Vant

export default {
  data() {
    return {
      value:"",
      show_border:false,
      // 
      active: 0,

      // 
      classify:[],
      // 
      diarys: [],
      // 
      page: 1,
      //
      pagecount:0,


      // 
      // disabled:false,

      // 
      avatar:true,
      // 
      isFixed:false,
      // 
      nva_icon_color:"#fff",
      // 
      backgroundColor:"transparent",
    }
  },
  methods:{
    // 
    handleScroll () {
      // 
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      // dom
      let nav_title = document.getElementsByClassName("van-nav-bar__title")[0];
      // 46
      if (scrollTop >= 287) {
        this.backgroundColor = "#fff";
        this.nva_icon_color = "#5ABCC8";
        nav_title.style.color = "#5ABCC8";
        this.show_border = true;
       } else { // 
        this.backgroundColor = "transparent"
        this.nva_icon_color = "#fff"
        nav_title.style.color = "#fff";
        this.show_border = false;
      }
    },


    // 
    preview(images,index){
      //  URL 
      let pic = [];
      images.forEach((value, index, array) => {
        //              url    
        pic.push( `http://127.0.0.1:8888/journal-pic/${value.picture_pic}`);
      }); 
      // ImagePreview 
      ImagePreview({
        images:pic, // URL 
        showIndex:true, //
        loop:false, //
        startPosition:index //
      });
    },


    // 
    loaddata(value) {
      // 
      // console.log(value);
      //  active  
      this.axios.get('/journal/diary?cid=' + value).then( res => {
        this.diarys = [];
        // -- 
        this.diarys = res.data.result;
        // console.log(res.data);
      })
    },


    //  
    myInfo(){
      let isOnlogin = this.$store.state.isOnlogin;
      if ( isOnlogin == 1) {
        this.$router.push("/personage");
      } else {
        this.$toast.fail('');
        this.$router.push("/login");
      }
    }



  },
  watch:{
    //
    active(value) {

      //1(1)
      this.page = 1;

      // 
      this.loaddata(value);

      // this.axios.get('/journal/diary?cid=' + value).then( res => {
      //   this.diarys = [];
      //   this.diarys = res.data.result;
      // })
    },
  },
  mounted() {
    // 
    this.axios.get('/journal/classify').then( res => {
      this.classify = res.data.result;
    })

    // 
    this.loaddata(this.active);
    
    // this.axios.get('/journal/diary?cid=' + this.active +"&page=" + page).then( res => {
    //   this.diarys = res.data.result;
    // })

    //store
    this.$store.dispatch("obtain_classify");

    window.addEventListener('scroll', this.handleScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  },
}
</script>
